.link_underline::before, .footer__community .community__link::before, .header__link-key::before, .menu-float__menu-link::before, .menu-float__link::before, .link_underline::after, .footer__community .community__link::after, .header__link-key::after, .menu-float__menu-link::after, .menu-float__link::after {
